
import {
  defineComponent,
  onMounted,
  ref,
  toRef,
  watch,
} from 'vue';

import ListCard from './list/CListCard.vue';

export default defineComponent({
  props: ['list'],
  components: { ListCard },
  setup(props) {
    const innerEl = ref<HTMLElement | null>(null);
    const cards = toRef(props, 'list');
    const innerStyles = ref({});
    const step = ref('0px');
    const transitioning = ref<boolean>(false);

    const setStep = () => {
      const innerWidth = (innerEl.value as HTMLElement).scrollWidth;
      const totalCards = cards.value.length;
      step.value = `${innerWidth / totalCards}px`;
    };

    const moveLeft = () => {
      innerStyles.value = {
        transform: `translateX(-${step.value})
                    translateX(-${step.value})`,
      };
    };

    const moveRight = () => {
      innerStyles.value = {
        transform: `translateX(${step.value})
                    translateX(-${step.value})`,
      };
    };

    const afterTransition = (callback: any) => {
      const listener = () => {
        callback();
        (innerEl.value as HTMLElement).removeEventListener('transitionend', listener);
      };
      (innerEl.value as HTMLElement).addEventListener('transitionend', listener);
    };

    const resetTranslate = () => {
      innerStyles.value = {
        transition: 'none',
        transform: `translateX(-${step.value})`,
      };
    };

    const next = () => {
      if (transitioning.value) return;

      transitioning.value = true;

      moveLeft();

      afterTransition(() => {
        console.log(11);
        const card = cards.value.shift();
        cards.value.push(card!);
        resetTranslate();
        transitioning.value = false;
      });
    };

    const prev = () => {
      if (transitioning.value) return;

      transitioning.value = true;

      moveRight();

      afterTransition(() => {
        const card = cards.value.pop();
        cards.value.unshift(card!);
        resetTranslate();
        transitioning.value = false;
      });
    };

    const scrolling = (e: any) => {
      const { clientHeight, scrollHeight, scrollTop } = e.target;

      // if (scrollTop + clientHeight >= scrollHeight) {
      console.log('Yay!');
      // }
    };

    onMounted(() => {
      setStep();
      resetTranslate();
    });

    return {
      cards,
      innerEl,
      innerStyles,
      next,
      prev,
      scrolling,
    };
  },
});
